import React ,{useState, useEffect} from 'react';
import Header from "./components/Header/Header";
import Main from "./components/Main/Main";
import styled , {css} from 'styled-components';


function App() {

  var formData = new FormData();

  const [data, setData] = useState([]);
  const [openModal, setOpenModal] = useState(false);
  const [showUsers, setShowUsers] = useState(1);

  const [postSucces, setPostSucces] = useState(false);

  const [validateError, setValidateError] = useState(false);

  const [myFormData, setFormData] = useState({
    name: '',
    email: '',
    phone: 0,
    positions: 0,
    photo: null
  })
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    formData.append('position_id', myFormData.positions); 
    formData.append('name', myFormData.name); 
    formData.append('email', myFormData.email); 
    formData.append('phone', myFormData.phone); 
    formData.append('photo', myFormData.photo); 
  },[myFormData])

  useEffect(() => {
    if(postSucces){
      setOpenModal(true);
      setFormData(prev => {})
    }else{
      setOpenModal(false);
    }
  },[postSucces])



  function FetchData(num, newCount){
    setLoading(true);
    let baseCount = 6;
    let url = `https://frontend-test-assignment-api.abz.agency/api/v1/users?page=${num}&count=${baseCount}`
    fetch(url)
      .then(res => res.json())
      .then(res => setData((prev) => {
        setLoading(false);
        if(res.users === undefined || res.users.length !== 6){
          return [...prev]
        }else{
          if(prev.length < 1){
            return [ ...res.users]
          }else if(newCount){
            baseCount = 1;
            let newArr = [...prev];
            newArr.unshift(res.users[0]);
            newArr.pop();
            return newArr
          }else{
            return [...prev , ...res.users]
          }
        }
      }
      ))
      .catch(error => console.log(`Error: ${error}`))
  }

  async function postDataItem(){

    const response = await fetch('https://frontend-test-assignment-api.abz.agency/api/v1/token');

    if (!response.ok) {
      const message = `An error has occured: ${response.status}`;
      throw new Error(message);
    }

    const key = await response.json();

    function postData(key){

      fetch('https://frontend-test-assignment-api.abz.agency/api/v1/users', 
          {
            method: 'POST',
            body: formData,
            headers: {
                'Token': `${key}`
            }, 
          }) 
            .then((response) => { return response.json()}) 
            .then((data) =>  { 
                if(data.success){
                     // process success response 
                     FetchData(1,1);
                     setPostSucces(true);
                     console.log('');
                }else { 
                    // proccess server errors 
                    setValidateError(true);
                    console.error(`    : ${data.message}`);
                }
            })
            .catch((error) => { 
              setValidateError(true);
                // proccess network errors 
            });
    }


    return postData(key.token)
  }

  useEffect(() => {
    FetchData(showUsers);
    setShowUsers(prev => prev += 1)
  },[])

  return (
    <GlobalWrap openModal={openModal} bp={100}>
        <Header />
        <Main 
          open={openModal} 
          setOpen={setOpenModal}
          name="about"
          id="about" 
          formData={myFormData}
          setFormData={setFormData}
          data={data}
          FetchData={FetchData}
          setShowUsers={setShowUsers}
          showUsers={showUsers}
          postData={postDataItem}
          postSucces={postSucces}
          setPostSucces={setPostSucces}
          validateError={validateError} 
          setValidateError={setValidateError}
          loading={loading}/>

    </GlobalWrap>
    
  );
}

const GlobalWrap = styled.div(props => {
  let botPadding = 0;
  let bgColor = "#F8F8F8";
  let overflow = 'auto';
  let position = 'initial';

  if(props.bp){
    botPadding = props.bp
  }
  if(props.bg){
    bgColor = props.bg.color
  }
  if(props.openModal){
    overflow = 'hidden';
    position = 'fixed';
  }else{
    overflow = 'auto';
    position = 'initial';
  }
  return css`
    width: 100%;
    position:${position};
    overflow:${overflow};
    padding-bottom:${botPadding}px;
    background: ${bgColor};
  `
})


const WrapSizeFull = styled.div((props) => {
  let dynPadding = [0,0,0,0];
  if(props.pad){
    for(let i = 0;i<props.pad.length; i++){
      dynPadding[i] = props.pad[i];
    }
  }
  let displayFlex = "basic";
  let justifyContent = "none";
  let marginBot = 0;
  let flexDirection = "none;"

  if(props.fx){
    flexDirection = props.fx
  }
  if(props.dx === "true"){
    displayFlex = "flex";
  }
  if(props.jc){
    justifyContent = props.jc
  }
  if(props.mb){
    marginBot = props.mb
  }
  return css`

      width: 1170px ;
      margin: 0 auto;
      padding-top: ${dynPadding[0]}px;
      padding-right: ${dynPadding[1]}px;
      padding-bottom: ${dynPadding[2]}px;
      padding-left: ${dynPadding[3]}px;
      display: ${displayFlex};
      justify-content:${justifyContent};
      margin-bottom:${marginBot}px;
      flex-direction:${flexDirection};

      @media (max-width: 1169px) {
        width: 100%;
      }
      @media (max-width: 768px) {
       
      }
      @media (max-width: 360px) {

      }
  `
})

const WrapListContainer = styled.div(props => {
  let marginTop = 0;
  let marginBottom = 0;

  if(props.mt){
    marginTop = props.mt
  }
  if(props.mb){
    marginBottom = props.mb
  }
  return css`
  display:flex;
  flex-direction:column;
  align-items:center;
  margin-top:${marginTop}px;
  h2{
    font-weight: 400;
    font-size: 40px;
    text-align:center;
    margin-bottom:${marginBottom}px;
  }
  `
})




  




export { WrapSizeFull , GlobalWrap , WrapListContainer };
export default App;
