import React,{Fragment} from 'react'
import styled,{css} from 'styled-components';

import { Link, animateScroll as scroll } from 'react-scroll'

const Button = ({title , mr, mt, func, colorText, bg, link, where}) => {
  return (
    <Fragment>
    {
      link? 
      <LinkStyle
        to={where}
        spy={true} 
        smooth={true} 
        offset={-170} 
        duration={500} 

        mr={mr}
        mt={mt}
        colorText={colorText}
        bg={bg}
      >{title}</LinkStyle> :
      <BtnStyle 
        onClick={func}
        mr={mr}
        mt={mt}
        colorText={colorText}
        bg={bg}>{title}
    </BtnStyle>
    }
    </Fragment>
  )
}
const LinkStyle = styled(Link)((props) => {
  let marginTop = 0;
  let marginR = 0;
  let colorText = '';
  let bg = "";
  if(props.mr){
    marginR = props.mr
  }
  if(props.mt){
    marginTop = props.mt
  }
  if(props.colorText){
    colorText = props.colorText
  }else{
    colorText = '#000'
  }
  if(props.bg){
    bg = props.bg
  }else{
    bg = "#F4E041"
  }


  return css`
    background-color: ${bg};
    padding: 8px 22px;
    border: 2px solid transparent;
    border-radius: 80px;
    color: ${colorText};
    font-size:16px;
    forn-weigth:400;
    margin-right: ${marginR}px;
    margin-top: ${marginTop}px;
    transition:all .2s;
    :hover{
      cursor:pointer;
      background-color:#FFE302;
    }
  `
})

const BtnStyle = styled.button((props) => {
  let marginTop = 0;
  let marginR = 0;
  let colorText = '';
  let bg = "";
  if(props.mr){
    marginR = props.mr
  }
  if(props.mt){
    marginTop = props.mt
  }
  if(props.colorText){
    colorText = props.colorText
  }else{
    colorText = '#000'
  }
  if(props.bg){
    bg = props.bg
  }else{
    bg = "#F4E041"
  }


  return css`
    background-color: ${bg};
    padding: 8px 22px;
    border: 2px solid transparent;
    border-radius: 80px;
    color: ${colorText};
    font-size:16px;
    forn-weigth:400;
    margin-right: ${marginR}px;
    margin-top: ${marginTop}px;
    transition:all .2s;
    :hover{
      cursor:pointer;
      background-color:#FFE302;
    }
  `
})


export default Button