import React, {useState, useEffect} from 'react'
import styled from 'styled-components';
import {WrapListContainer} from '../../../App';
import TextField from './Inputs/TextField';
import UploadInput from './Inputs/UploadInput';
import SelectButtons from './SelectButtons/SelectButtons';
import Button from '../../Button/Button';

import {Element, animateScroll as scroll} from 'react-scroll';

const CreateUserForm = ({
    postData, 
    formData, 
    setFormData, 
    setPostSucces,
    setValidateError,
    validateError,
    postSucces
}) => {

    const [error, setError] = useState(false);

    const [name, setName] = useState('');
    const [mail, setMail] = useState('');
    const [phone, setPhone] = useState('');
    const [specialization, setSpecialization] = useState('');
    const [fileUpload, setFileUpload] = useState(null);
    const [positions, setPositions] = useState(null);

    function changeName(e){
        if(e.target.value.length >= 2 && e.target.value.length < 50){
            setName(prev => prev = e.target.value);
            setValidateError(false)
        }else{
            setError(true)
        }
    }
    function changeMail(e){
        setMail(prev => prev = e.target.value);
        setValidateError(false)
    }

    function changePhone(e){
        setPhone(prev => prev = e.target.value);
        setValidateError(false)
    }

    function changeSpecialization(e){
        setSpecialization(e.target.value)


        async function postDataItem(){
            const response = await fetch('https://frontend-test-assignment-api.abz.agency/api/v1/positions');
        
            const data = await response.json();

            data.positions.forEach((el) => {
                    if(el.name === specialization){
                        setPositions(prev => prev = el.id)
                    }
                }
            )
            console.log(positions);

        }
        postDataItem()
        
        console.log(formData);
    }

    let typeOfproperfty = {
        num : "number",
        string: 'string',
        all: 'all'
    }

    useEffect(() => {
        setFormData((prev) => {
           let update = {
                name:name, 
                email:mail, 
                phone:phone, 
                positions:positions,
                photo:fileUpload
            };
           return {...update}
        })
    
    },[name, mail, phone, positions, fileUpload])

    useEffect(() => {
        console.log(formData)
    },[name, mail, phone, formData, fileUpload])


  return (
    <WrapListContainer mt={140}>

        <h2>Working with POST request</h2>
        <WrapSizeForInputs
            name="form"
            id="form"
        >
            <TextField 
                validateError={validateError}
                funk={[changeName, {type:typeOfproperfty.all}]}
                placehold={"Your name"}
                helper={validateError ? "Error" : null}/>
            <TextField 
                validateError={validateError}
                funk={[changeMail, {type:typeOfproperfty.all}]}
                placehold={"Email"}
                helper={validateError ? "Error" : null}/>

            <TextField 
                validateError={validateError}
                funk={[changePhone, {type:typeOfproperfty.num}]}
                placehold={"Phone"}
                helper={validateError ? "Error" : '+38 (XXX) XXX - XX - XX'} />

            <SelectButtons
                val={specialization}
                funk={changeSpecialization}
             />

            <UploadInput
                fileUpload={fileUpload}
                setFileUpload={setFileUpload} />

            <Button
                func={postData}
                title={"Sign up"}
                colorText={"rgba(255, 255, 255, 0.87)"}
                bg={"#B4B4B4"} />

        </WrapSizeForInputs>
    
    </WrapListContainer>
  )
}

const WrapSizeForInputs = styled(Element)`
    max-width: 382px;
    margin: 0 auto;
    display:flex;
    flex-direction:column;
    button{
        align-self:center;
    }
    @media (max-width: 768px) {
        width: 100%;
    }
`

export default CreateUserForm