import { margin } from '@mui/system';
import React, { useState, useEffect, ChangeEvent, Fragment } from 'react';
import styled, { css } from "styled-components";

const UploadInput = ({setFileUpload, fileUpload}) => {

  const [file, setFile] = useState(null);
  const [name, setName] = useState("Upload your photo");

  const [eror, setError] = useState(false);

  const [erorCause , setErorCause] = useState('');

  function changerFileAndName(e){
    if(e.target.files.length > 0){
      setFile(e.target.files[0]);
    }else{
      setFile(null)
    }
  }

  useEffect(() => {
    if(file !== null && file){
      setFileUpload(file)
    }
  },[file])

  function checkTypeFile(str){
    if(str.includes('.')){
      let dot = 0;
      for(let i = 0;i<str.length;i++){
        if(str[i] === "."){
          dot = str.indexOf(str[i]);
        }
      }
      let checkType = str.slice(dot, str.length);

      if(checkType !== ".jpg" && checkType !== ".jpeg"){
        setError(true)
        setErorCause('  ')
      }else{
        setError(false)
        setErorCause('')
      }
    }
  }

  useEffect(() => {
    if(file !== null || file === undefined){
      const maxSize = 5000000;

      // console.log(file);
      if(file.size <= maxSize){
        if(file.name){
          checkTypeFile(file.name)
        }
        setName(prev => prev = file.name)
      }else{
        setError(true);
        setErorCause('  ')
      }

    }else{
      setName(prev => prev = "Upload your photo");
      setError(false)
    }
  },[file, name])

  useEffect(() => {
    console.log(file);
  },[file])


  return (
    <Fragment>
      <ContainerWrap
        error={eror ? "yes" : "no"}
      >
        <input 
          id="kaka" 
          type={"file"} 
          placeholder="Upload your photo"
          onChange={changerFileAndName}>
        </input>  

        <label 
          htmlFor="kaka">
          <span>Upload</span>
          <span>{name}</span>
        </label>

        {
          eror ? <ErrorBlock>Error : {erorCause}</ErrorBlock> : null
        }

      </ContainerWrap>

    </Fragment>
  );
}

const ContainerWrap = styled.div((props) => {

  let colorError = '#D0CFCF';
  let borderWidthError = 1;
  let labelColorBorder = 'rgba(0, 0, 0, 0.87)';
  let mb = 50;
  let zeroBorder = 1;
  let colorPlacehold = '#7E7E7E';
  let hoverColorBorder = 'rgba(0, 0, 0, 0.87)';

  if(props.error === "yes"){
    colorError = '#CB3D40';
    borderWidthError = 2;
    labelColorBorder = '#CB3D40';
    zeroBorder = 0;
    hoverColorBorder = '#CB3D40';
  }


  return css `
    display:flex;
    width:100%;
    position: relative;
    margin-top:45px;
    margin-bottom:${mb}px;
    button{
      padding: 14px;
      border:1px solid rgba(0, 0, 0, 0.87);
      background-color:#fff;
      font-weight: 400;
      font-size: 16px;
      color: rgba(0, 0, 0, 0.87);
      border-radius: 4px 0px 0px 4px
    }
    input{
      border:1px solid ${colorError}; 
      color: #7E7E7E;
      padding: 14px;
      font-weight: 400;
      font-size: 16px;
      border-radius: 4px;
      outline:none;
      width:100%;
      opacity:0;
    }
    label{
      border:${borderWidthError}px solid ${colorError};
      border-radius:4px;
      display:flex;
      width:100%;
      position: absolute;
      left:0;
      top:0;
      text-align:center;
      align-items:center;
      padding: 4px 16px 4px 16px;
      height:100%;
      transition:all .2s;
      span:first-child{
        height:calc(100% + 2px);
        padding: 14px;
        border:${zeroBorder}px solid ${labelColorBorder};
        border-right:${borderWidthError}px solid ${labelColorBorder};
        border-radius: 4px 0px 0px 4px;
        position:absolute;
        left:-1px;
        top:-1px;
        bottom:-1px;
        width:83px;
        display:flex;
        align-items:center;
        justify-content:center;
      }
      span:last-child{
        position:absolute;
        height:100%;
        left:83px;
        display:flex;
        align-items:center;
        justify-content:center;
        padding: 4px 16px 4px 16px;
        color: ${colorPlacehold};
      }
    }
    :hover{
      label{
        border:${borderWidthError}px solid ${hoverColorBorder};
      }
    }
  `
})

const ErrorBlock = styled.span`
  color:#CB3D40;;
  position: absolute;
  left: 14px;
  bottom: -24px;
  width: 100%;
  font-weight: 400;
  font-size: 12px;
`

export default UploadInput
