import React,{Fragment} from 'react';
import styled , {css} from 'styled-components';

const UserCard = ({img, title, text, number, mail}) => {
    let titleExample = "No title..."
    let textExample = "No text...";
    let imgExample = "";
    let mailExample = "@gmail.com";
    let numberExample = "+380...";

    let mailCorrect = '';
    let titleCorrect = '';

    if(mail.length > 40){
        mailCorrect = mail.slice(0 , 10) + " ...";
    }else{
        mailCorrect = mail
    }
    if(title.length > 40){
        titleCorrect = title.slice(0 , 10) + " ...";
    }else{
        titleCorrect = title
    }

  return (
    <Fragment>
        <Card>
            <img src={img} />
            <p>{title ? titleCorrect : titleExample}</p>
            <p>{text ? text : textExample}</p>
            <p style={{cursor:"pointer"}}>{mail ? mailCorrect : mailExample}</p>
            <p>{number ? number : numberExample}</p>
        </Card>
    </Fragment>
  )
}

const Card = styled.li((props) => {
    return css`
        border-radius: 10px;
        background-color:#fff;
        width: 23.125rem;
        height: 254px;
        padding:20px;
        text-align:center;
        p{
            width:100%;
            font-weight: 400;
            font-size: 16px;
            line-height: 26px;
        }

        img{
            width:70px;
            height:70px;
            border-radius:50%;
        }
        p:first-of-type{
            margin-top:20px;
        }
        @media (max-width: 1024px) {
            width: 282px;
        }
        @media (max-width: 768px) {
            width: 344px;
        }
        @media (max-width: 360px) {
            width: 328px;
        }
    `
})



export default UserCard