import React,{useState, useRef, useEffect} from 'react';
import styled, {css} from 'styled-components';
import FormControl from '@mui/joy/FormControl';
import FormHelperText from '@mui/joy/FormHelperText';
import Input from '@mui/joy/Input';

const TextField = ({helper, placehold, funk, error, validateError}) => {

  const [stateTextUpper, setStateTextUpper] = useState(false);

  const inputRef  = useRef(null);

  function focusChanger(e) {
    if(e.target.value.length >= 1){
      return null
    }else{
      setStateTextUpper(!stateTextUpper);
    }
    
    if(stateTextUpper){
      inputRef.current.focus();
    }
  }

  function blurTarget(e) {
    if(e.target.value.length > 1){
      return null
    }else{
      setStateTextUpper(false)
    }
  }


  return (
    <WrapFormControl 
      placehold={placehold}
      stateTextUpper={stateTextUpper}
      validateError={validateError}

    >
        {placehold ? 
              funk ?
              <InputElements
                onChange={funk[0]}
                notchedOutline={true}
                ref={inputRef} 
                onBlur={(e) => blurTarget(e)}
                onFocus={(e) => focusChanger(e)}
                validateError={validateError}
                placeholder={placehold} /> 
              : 
              <InputElements
                onFocus={() => setStateTextUpper(true)}
                validateError={validateError} 
                placeholder={placehold}/>
                  : 
        <Input
            onFocus={() => setStateTextUpper(true)}
            validateError={validateError}
            placeholder="Empty" />}

        
        {helper ?  <HelperBotText style={validateError ? {color:"red"} : {color: "#7E7E7E"}}>{helper}</HelperBotText> : null}

    </WrapFormControl>
  );
}

const WrapFormControl = styled(FormControl)((props) => {

  let checkerText = "none";
  let colorText = '#7E7E7E';

  if(props.validateError){
    colorText = "red"
  }else{
    colorText = '#7E7E7E';
  }


  if(props.stateTextUpper){
    checkerText = "block";
  }else{
    checkerText = "none";
  }

  return css`

  input:focus{
    outline: none !important;
  }
  div:first-child{
    :focus-visible{
      outline: none !important;
      border:none !important;
    }
    :before{
      display:none;
    }


  }
  
    :after{
      display:${checkerText};
      content:"${props.placehold}";
      color:${colorText};
      position:absolute;
      left:16px;
      top:38px;
      height:14px;
      padding:4px 4px;
      background-color:#F8F8F8;
      font-weight: 500;
      font-size: 12px;
      animation:visible 0.5s 1;
      @keyframes visible {
        from {
          opacity:0;
          font-size: 16px;
          transform: translateY(20px)
        }
        to {
          opacity:1;
          font-size: 12px;
        }
      }
    }
  `
})  

const HelperBotText = styled(FormHelperText)`
    font-weight: 400 !important;
    font-size: 12px !important;
    color:#7E7E7E;
    margin-left:18px !important;
    position: absolute;
    left: 0;
    bottom: -18px;
`
const InputElements = styled(Input)((props) => {
  let bordColor = '#D0CFCF';
  let placeholdColor = '#7E7E7E';
  let textColor = "#000";
  let borderWidth = 1;

  if(props.validateError){
    bordColor = "red"
    borderWidth = 2;
  }else{
    bordColor = "'#D0CFCF"
  }


  return css `
    :focus{
      &::placeholder{
        display:none !important;
      }
    }
    focus-visible:none !important;
    color:${textColor} !important;
    padding-left:16px !important;
    background-color:#F8F8F8 !important;
    height:54px;
    width:100%;
    margin-top: 50px;
    border: ${borderWidth}px solid ${bordColor} !important;
    border-radius: 4px;
    background-color:#F8F8F8 !important;
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
    input:focus{
      outline: none !important;
    }
    :placeholder{
      color:${placeholdColor} !important;
    }
    div:first-of-type{
      position:relative;
      focus-visible:none !important;
    }
  `
})




export default TextField;