import React from 'react'
import styled ,{css} from 'styled-components';
import { WrapSizeFull } from '../../App';
import backGround from '../../Assets/pexels-alexandr-podvalny-1227513.jpeg'
import Button from '../Button/Button';
import CreateUserForm from './CreateUserForm/CreateUserForm';
import SuccesComponent from './CreateUserForm/SuccesComponent/SuccesComponent';
import MemberList from './MemberList/MemberList';

const Main = ({
  data, 
  FetchData, 
  setShowUsers, 
  showUsers, 
  postData,
  formData,
  setFormData,
  postSucces,
  setPostSucces,
  setValidateError,
  validateError,
  loading,
  open,
  setOpen
}) => {

  return (
    <WrapSizeFull 
      jc={"center"} 
      dx={"column"}>
        <BlockWithImage>
          <BlockShadowFilter />
          <TitleBlockMain>
            <WrapText>
              <h1>Test assignment for front-end developer</h1>
              <p>What defines a good front-end developer is one that has skilled knowledge of HTML, CSS, JS with a vast understanding of User design thinking as they'll be building web interfaces with accessibility in mind. They should also be excited to learn, as the world of Front-End Development keeps evolving.</p>
              <Button 
                where={'form'}
                link={'yes'}
                mt={32} 
                title={"Sign up"} />
            </WrapText>
          </TitleBlockMain>
        </BlockWithImage>

        <MemberList 
          loading={loading}
          showUsers={showUsers}
          setShowUsers={setShowUsers}
          data={data}
          FetchData={FetchData} />
          {
            postSucces ?
            <SuccesComponent open={open} setOpen={setOpen} />
            :
            null
          }

          <CreateUserForm
            setValidateError={setValidateError}
            validateError={validateError}
            formData={formData}
            setFormData={setFormData}
            postData={postData}
            postSucces={postSucces}
            setPostSucces={setPostSucces} />



          
        
    </WrapSizeFull>
  )
}

const BlockWithImage = styled.div(props => {
  return css`
    margin-bottom:140px;
    z-index:2;
    width: 100%;
    position:relative;
    height: 650px;
    background-image:url(${backGround});
    background-repeat:no-repeat;
    background-size:cover;
    background-position:bottom;
  `
})

const TitleBlockMain = styled('div')`
  z-index:2;
  position: absolute;
  left:0;
  top: 0;
  width:100%;
  height:100%;
  text-align:center;
  h1,p{
    color: #fff;
    font-weight: 400;
  }
  h1{
    font-size: 40px;
  }
  p{
    line-height: 26px;
    font-size: 16px;
  }
  display:flex;
  align-items:center;
  justify-content:center;
`
const BlockShadowFilter = styled('div')`
  content:'';
  z-index:2;
  position: absolute;
  left:0;
  top: 0;
  width:100%;
  height:100%;
  background-color:rgba(0,0,0,0.4);
`
const WrapText = styled('div')`
  display:flex;
  align-items:center;
  justify-content:center;
  flex-direction:column;
  width: 23.75rem;
  @media (max-width: 360px) {
      width: 328px;
  }
`





export default Main