import React,{useState, useEffect} from 'react'
import ImageBg from "../../../../Assets/success-image.svg";
import {WrapListContainer} from '../../../../App';
import {Element, animateScroll as scroll} from 'react-scroll';
import styled, {css} from 'styled-components';

const SuccesComponent = ({open, setOpen}) => {
  
  useEffect(() => {
    if(open){
      setTimeout(() => setOpen(false),4000)
    }
  },[open])


  return (
    <GlobalWidth open={open} onClick={() => setOpen(false)}>
        <TextTitle
            name="form"
            
        >User successfully registered</TextTitle>
        <img src={ImageBg} alt="bg" />
    </GlobalWidth>
  )
}

const GlobalWidth = styled.div(props => {

  let displayComponent = 'flex';

  if(!props.open){
    displayComponent = 'none';
  }

  return css`
    position:fixed;
    left: 0;
    top: 0;
    width:100vw;
    height: 100vh;
    z-index:999;
    background-color:#fff;
    display:${displayComponent};
    align-items:center;
    justify-content:center;
    flex-direction:column;
    :hover{
      cursor:pointer;
    }
  `

})

const TextTitle = styled(Element)`
    font-weight: 400;
    font-size: 40px;
    text-aligt:center;
    color: rgba(0, 0, 0, 0.87);
    margin-bottom:50px;
`

export default SuccesComponent