import React , {useState, useEffect} from 'react';
import styled from 'styled-components';
import FormControl from '@mui/joy/FormControl';
import FormLabel from '@mui/joy/FormLabel';
import Radio from '@mui/joy/Radio';
import RadioGroup from '@mui/joy/RadioGroup';
import { createTheme } from '@mui/material/styles';

const SelectButtons = ({funk, val}) => {

  return (
    <FormControl style={{marginTop:"44px"}}>
      <FormLabel>Select your position</FormLabel>
      <RadioGroup RadioGroup
        defaultValue="1"
        name="controlled-radio-buttons-group"
        value={val}
        onChange={funk}
        sx={{ my: 1 }}
      >
        <RadioBtn value="Lawyer" label="Frontend developer" />
        <RadioBtn value="Content manager" label="Backend developer" />
        <RadioBtn value="Designer" label="Designer" />
        <RadioBtn value="Security" label="QA" />
      </RadioGroup>
    </FormControl>
  );
}


const RadioBtn = styled(Radio)`
  align-self:start !important;
  span:first-of-type{
    color:#00BDD3;
    span:checked{
      border-color:#00BDD3 !important;
      outline: 2px solid deeppink !important;
    }
  }
`



export default SelectButtons