import React ,{useState, useEffect} from 'react';
import styled from 'styled-components';
import Button from '../../Button/Button';
import UserCard from './UserCard/UserCard';
import {WrapListContainer} from '../../../App';

import {Element, animateScroll as scroll} from 'react-scroll';
import Spinner from './Spinner/Spinner';

const MemberList = ({data, FetchData, setShowUsers, showUsers, loading}) => {

  const [copyData , setCopyData] = useState([]);

  useEffect(() => {
    setCopyData((prev) => prev = data);
  },[showUsers])

  function FetchPlusIncUserList(){
    FetchData(showUsers);
    setShowUsers(prev => prev += 1)
  }

  return (
    <WrapListContainer mb={50}>
      <h2>Working with GET request</h2>


      <UsersContainer 
        name="users"
        id="users" >
        {
          data.map((el, id) => <UserCard 
            key={id}
            title={el.name}
            text={el.position}
            img={el.photo}
            mail={el.email}
            number={el.phone} />)
        }
      
      </UsersContainer>

      {
        loading ? <Spinner /> :

        data.length === copyData.length ? null :  
        <Button 
          func={() => FetchPlusIncUserList()}
          title={"Show more"}/>
      }


      
    </WrapListContainer>
  )
}

const UsersContainer = styled(Element)`
  display:flex;
  flex-wrap:wrap;
  gap:29px;
  list-style:none;
  justify-content:center;
  margin-bottom:50px;
  
`



export default MemberList