import React from 'react'
import styled from 'styled-components';
import { WrapSizeFull , GlobalWrap } from '../../App';
import Button from '../Button/Button';
import { ReactComponent as LogoSvg } from '../../Assets/Logo.svg';

const Header = () => {

  return (
    <GlobalWrap style={{position:"sticky", top:"0px",left:"0px", zIndex:"999"}} bg={{color:"#fff"}}>
        <WrapSizeFull 
            dx={"true"} 
            pad={[13,0,13,0]}
            jc={'space-between'}>
            <WrapLogo>
                <LogoSvg />
            </WrapLogo>

            <WrapButtons>
                <Button 
                    where={'users'}
                    link={'yes'}
                    mr={10} 
                    title={'Users'} />
                <Button 
                    where={'form'}
                    link={'yes'}
                    title={'Sign up'} />
            </WrapButtons>
        </WrapSizeFull>
    </GlobalWrap>
  )
}


const WrapButtons = styled('div')`
    display:flex;
`
const WrapLogo = styled('div')`

`

export default Header

